* {
  box-sizing: border-box;
}

body {
  color: #212121;
  background-color: #fafafa;
  font-family: Open Sans, Helvetica Neue, sans-serif;
  line-height: 1;
}

.search-form {
  font: inherit;
  height: 60px;
  background-color: #3f57b3;
  justify-content: center;
  align-items: center;
  margin-bottom: 16px;
  margin-left: auto;
  margin-right: auto;
  padding: 10px 8px;
  display: flex;
}

.search-btn {
  cursor: pointer;
  transform: scaleX(-1);
